import React from 'react'
import './city.css'
import Img1 from '../images/55555.png'
import { Link } from 'react-router-dom'

class City extends React.Component {
  state={
    tab:["","","","","","","","","","","","","","","",""]
  }

  render () {
    let {tab}=this.state
    let Citym=tab.map((item,index)=>{
      return(
        <span key={index}>{item}</span>
      )
    })


    return(
      <div className='city'>
        <div className="top">
          <img onClick={()=>this.props.history.goBack()} src={Img1} alt=""/>
          <span></span>
        </div>
        <input type="text" value='/'/>
        <div className="ttp">
          <h3><Link to='/home/city'></Link></h3>
          <h3><Link to='/home/citys'></Link></h3>
        </div>
        <div className="min1">
          <h3></h3>
          <div className="obj">
            <span></span>
          </div>
          <h3></h3>
          <div className="obj">
            <span></span>
            <span></span>
            <span></span>
          </div>
          <h3></h3>
          <div className="obj">
            {Citym}
          </div>
          <h3>A</h3>
            <p></p>
            <p></p>
            <p></p>
            <p></p>
            <p></p>

        </div>
      </div>
    )
  }
}

export default City;



