import React from 'react'
import './message.css'
import Img1 from '../images/29.svg'
import Img2 from '../images/35.png'
import Img3 from '../images/46.png'
import Img18 from '../images/u14.png'
import Img19 from '../images/u21.png'
import Img20 from '../images/u32.png'
import Img22 from '../images/u52.png'

import Img26 from '../images/u48.png'
import {Link} from 'react-router-dom'

class Message extends React.Component {
  style={
    tab:[
      Img2,
      Img3
    ]
  }
  render () {
    return(
      <div className='message'>
        <div className="foundTop">
          <img src={Img1} alt=""/>
        </div>
        <div className="messagemin">
          <img src={Img2} alt=""/>
          <div className="messagein">
            <p></p>
            <div className="mess">
              <span></span>
              <span>17:43</span>
            </div>
          </div>
        </div>
        <div className="messagemin">
          <img src={Img3} alt=""/>
          <div className="messagein">
            <p></p>
            <div className="mess">
              <span>30</span>
              <span>01:25</span>
            </div>
          </div>
        </div>
        <div className="messagemin">
          <img src={Img3} alt=""/>
          <div className="messagein">
            <p></p>
            <div className="mess">
              <span>200</span>
              <span>2017-01-15</span>
            </div>
          </div>
        </div>
        <div className="homeFd">
          <ul>
            <li>
              <Link to='/home'>
                <img src={Img18} alt=""/>
                <p></p>
              </Link>
            </li>
            <li>
              <Link to='/found'>
                <img src={Img19} alt=""/>
                <p></p>
              </Link>
            </li>
            <li>
              <Link to='/release'>
              <img src={Img20} alt=""/>
              <p></p>
              </Link>
            </li>
            <li>
              <Link to='/messsage'>
              <img src={Img26} alt=""/>
              <p></p>
              </Link>
            </li>
            <li>
              <Link to='/my'>
                <img src={Img22} alt=""/>
                <p></p>
              </Link>
            </li>

          </ul>

        </div>
      </div>
    )
  }
}

export default Message;



