import React from 'react'
import Img1 from '../images/55555.png'
import { Link } from 'react-router-dom'

class Citys extends React.Component {
  state={
    tab:[
      '','','','','','','','','','','','','','','',
    ],
    tabs:[
      '','','','','','','','','','','','','','','','','','','',''
    ]
  }
  render () {
    let {tab} = this.state
    let {tabs} = this.state
    let tabss = tab.map((item,index)=>{
      return(
        <span key={index}>{item}</span>
      )
    })
    let cit = tabs.map((item,index)=>{
      return(
        <span key={index}>{item}</span>
      )
    })
    return(
      <div className='city'>
        <div className="top">
          <img onClick={()=>this.props.history.goBack()} src={Img1} alt=""/>
          <span></span>
        </div>
        <input type="text" value='/'/>
        <div className="ttp">
        <h3><Link to='/home/city'></Link></h3>
        <h3><Link to='/home/citys'></Link></h3>
        </div>
        <div className="min1">
          <h3></h3>
          <div className="obj">
            <span></span>
          </div>
          <h3></h3>
          <div className="obj">
            <span></span>
            <span></span>
            <span></span>
          </div>
          <h3></h3>
          <div className="obj">
            {tabss}
          </div>
          <h3></h3>
          <div className="obj">
            {cit}
          </div>
        </div>
      </div>
    )
  }
}

export default Citys;



